<template>
<div>
    <Workout v-if="req('Workouts')" :params="route('id')" />
    <Home v-else />
</div>

</template>
<script>
    import Workout from "../add/Workouts.vue"
    import Home from '../../views/Home.vue'
    export default {
        watch: {},
        components: {
            Workout,
            Home
        },
        created() {
            if (!this.route("id")) {
                // let name = this.route("name"),
                //  href = name ? name : '..';
                // location.href = `../${href}/`
            }
        },
        methods: {
            req(name) {
                let ro = this.route('name');
                if (ro == name) {
                    return true;
                } 
            },

            route(cases, param) {
                param = this.$route.params;
                cases = cases == "id" ? param.id : param.name;
                return cases;
            },
        }
    }
</script>